*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  min-height: 100vh;
  
}

.container{
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.controls{
  height: 50px;
  background-color: royalblue;
}

.bars{
  flex:1 1 auto;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 5px;
}

.bar{
  width: 12px;
  background-color: #868585;
}

.redBar{
  background-color: red;
}
.blueBar{
  background-color: #0027ff;
}
footer{
  height: 30px;
  width: 100%;
  background-color: royalblue;
}